.searchPage__logo {
    object-fit: contain;
    margin-right: 50px;
    height: 50px;
}

.searchPage__header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}

.searchPage__headerBody > .search > .search_input {
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}




.searchPage__options {
    display: flex;
    align-items: center;
    color: grey;
}


.searchPage__optionsRight,
.searchPage__optionsLeft {
    display: flex;
}

 


.searchPage__optionsRight a,
.searchPage__options a {
    text-decoration: none;
    color: gray;   
}

.searchPage__optionsRight {
    margin-left: 80px;
}

.searchPage__option {
   margin-right: 20px;
   display: flex;
   align-items: center;
}

.searchPage__option a {
    margin-left: 5px;
}

.searchPage__result {
    margin: 40px 0;
}

.searchPage__resultCount {
    color: #70757a;
    font-size: 14px;
}

.searchPage__results {
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
}

.searchPage__resultLink {
    text-decoration: none;
    display: flex;
}

.searchPage__resultTitle {
    text-decoration: none;
    
}

.searchPage__resultTitle:hover {
    text-decoration: underline;
}

.searchPage__resultTitle > h2 {
    font-weight: 500;
}

.searchPage__resultSnippet {
    margin-top: 10px;
}


.searchPage__resultImage {
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-bottom: 5px;
    
}